<template>
  <div id="app">
    <router-view />
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  mounted() {
    if (!this.token) {
      this.$router.push({
        name: "Index"
      });
    }
  },
  computed: {
    ...mapGetters(["token"])
  }
};
</script>
